@import url(https://fonts.googleapis.com/css?family=Raleway:200,100,400);
@import url(https://fonts.googleapis.com/css?family=Raleway:200,100,400);
.header-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 60%;
    align-self: center;
    color: rgb(46, 154, 173);

}

.header-container > h1 {
    font-style: normal;
    font-weight: 500;
    font-size: 2.9em;
    line-height: 57px;
    text-align: center;
    letter-spacing: 0.2px;
    color: rgb(46, 154, 173);
    margin: 0;
}

.header-container > p {
    /* font-family: Montserrat; */
    font-family: 'Raleway', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 1.4em;
    line-height: 1.4em;
    text-align: center;
    margin: 10px 0;
}
.photo{
    width: 300px;
    border-radius: 20px;
    box-shadow: 0px 4px 31px rgba(0, 0, 0,0.15);
    margin: 12px;
}

@media only screen and (max-width: 640px) {
    .header-container {
        width: 80%;
    }
    .header-container > p {
        font-size: 1.2em;
        line-height: 1.2em;
    }
}


.about-main {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 60px 80px 80px 80px;
    color: rgb(46, 154, 173);
}

.about-main-left,
.about-main-right {
    width: 50%;
}

.about-main-right {
    display: flex;
    justify-content: flex-end;
}

.about-main-left {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.about-link-element {
    color: rgb(46, 154, 173);

}

.about-sub-head {
    margin: 0;
}

.about-details {
    margin: 10px 0 32px 0;
    font-size: 30px;
}

.about-anime {
    width: 420px;
    max-width: 100%;
    max-height: 100%;
    border-radius: 30px;
}

.vector-frame {
    position: absolute;
    z-index: -10;
    left: 0;
    bottom: 0;
    width: 55%;
}

.about-vector {
    display: block;
    max-height: 100%;
    max-width: 100%;
}
.about-main-right > img {
    -webkit-clip-path: circle(50% at 50% 50%);
            clip-path: circle(50% at 50% 50%);
}

@media only screen and (max-width: 640px) {
    .vector-frame {
        width: 90%;
    }

    .about-head {
        width: 80%;
    }

    .about-head > p {
        font-size: 1.2em;
        line-height: 1.2em;
    }

    .about-main {
        padding: 50px 32px;
    }

    .about-main-left,
    .about-main-right {
        width: 100%;
    }
}
.contact-form-container{
    width: 50%;
    align-self: center;
    margin: 70px 0;

}

.contact-form{
    display: flex;
    flex-direction: column;
    align-self: center;
    justify-content: center;
}

.input-box{
    width: 100%;
    background-color: #fff;
    border: 1px solid #e8e8e8;
    margin: 12px 0;
    box-sizing: border-box;
    box-shadow: 0px 2px 2px rgb(0, 0,0,0.25);
    border-radius: 36px;
    padding: 18px 33px;
    font-family: 'Raleway', sans-serif;
    font-size: 16px;
    line-height: 16px;
}
.body-input{
    height: 250px;
    padding: 25px 35px;
}
.contact-btn{
    margin: 10px 0;
    border: none;
    color: #fff;
    font-weight: bold;
    font-family: 'Raleway', sans-serif;
    font-size: 16px;
    line-height: 16px;
    width: 100%;
    text-align: center;
    padding: 25px 35px ;
    background: linear-gradient(39.97deg,#7ac5cc 0.79%,#5292eb 79.2%);
    border-radius: 30px;
}

.social-icon-conatiner{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

}
.social-icon{
    -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
            filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    margin: 20px;
}
.social-icon:hover,.contact-btn:hover{
    -webkit-filter: drop-shadow(0px 9px 9px rgba(0, 0, 0, 0.35));
            filter: drop-shadow(0px 9px 9px rgba(0, 0, 0, 0.35));
    cursor: pointer;
    transition: all .3s ease-in-out;
}

@media only screen  and (max-width:640px) {
    .social-icon{
        margin: 8px 21px;
    }
    .contact-form-container{
        width: 80%;
    }
}
a{
    display: inline-block;
    
}
.icon{
    width: 31px;
