}
.home-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow-y: auto;
    padding: 100px 10%;
    box-sizing: border-box;
    min-height: 100vh;
    background:linear-gradient(39.97deg,#a2aeaf 0.79%,#458ff6 79.2%);
    
}
.header-text{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Sacramento',cursive !important;
    color: #fff;
    text-align: center;
    
}
.header-text>h1{
    font-style: normal;
    font-weight: 900;
    font-size: 3em;
    line-height: 62px;
    letter-spacing: 0.2px;
    color: #fff;
    margin: 10px;
}
.header-text>p{
