}
.head-btns{
    display: flex;
    margin: 35px 10px;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.btn{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0px 36px;
    height: 50px;
    left: 0;
    top: 0;
    text-decoration: none;
    box-shadow: 0px 4px 31px rgba(0, 0, 0,0.15);
    border-radius: 36px;
    margin: 6px;
}
.btn-white{
    background:#fff;
    color: #07152b;
}
.btn-transparent{
    border: 1px solid #fff;
    box-sizing: border-box;
    color: #fff;
}
.btn:hover{
    box-shadow: 0px 44px 31px rgba(0, 0, 0,0.155);
    transition: all 0.3 ease-in-out;
}
.btn-text{
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.2px;
    text-align: center;
}
.splash-img{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.home-anime{
    margin-bottom: -80px;
}


@media only screen and (max-height:550px){
