.project-card{
    display: flex;
    width: 100%;
    
}
.project-cards-container{
    display: flex;
    flex-wrap: wrap;
    padding: 30px;
    align-self: center;
    justify-content: center;
    
}
.project-card{
    width: 500px;
    min-height: 550px;
    background:#fff;
    /* box-shadow: 10px 40px 50px rgba(91, 100, 180, 0.69); */
