.image-container{
    width: 100%;
    border-radius: 20px 20px 0 0;

}
.project-link{
    border-radius: inherit;

}
.project-img{
    display: block;
    border-radius: inherit;
    max-height: 100%;
    max-width: 100%;

}
.project-details-container{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-self: center;
    padding: 20px;
}
.project-detail{
    font-weight: 300;
    font-size: 18px;
    line-height: 16px;
    color: #212523;
    line-height: 1.1;
}
.project-heading{
    margin: 0;
}
@media only screen and (max-width:640px) {
    .project-cards-container{
        /* width: 80%; */
        padding: 20px;
    }
    .project-card{
        width: 290px;
        min-height: 500px;
    }
}
.skill-card-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin: 80px 20%;
}

.skill {
    display: flex;
    flex-direction: column;
    width: 150px;
    align-items: center;
    margin-bottom: 20px;
}

.skill > img {
    width: 108px;
    margin-bottom: 15px;
    -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
            filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.skill > p {
    font-family: Montserrat;
    font-size: 24px;
    line-height: 36px;
    text-align: center;
    letter-spacing: -0.015em;
