/* position and size for burger-buuton */
.bm-burger-button{
    position: fixed;
    width: 36px;
    height: 36px;
    left: 32px;
    top: 32px;
}
/* Color to burger */
.bm-burger-bars{
    background:#373a47;
}
/* color to our burger icon on hover */
.bm-burger-bars-hover{
    background: black;
}
.bm-cross-button{
    height: 26px;
    width: 26px;
}
.bm-cross{
    background:white;
}
.bm-menu-wrap{
    position: fixed;
    height: 100vh;
    overflow-x: hidden;
    box-shadow: 0px 4px 31px rgba(0, 0, 0,0.15);
}
.bm-menu {
    background:linear-gradient(39.97deg,#a2aeaf 0.79%,#458ff6 79.2%);
    padding: 2.5em 1.5em 0;
    height: 100vh;
    font-size: 1.15em;
    overflow: hidden !important;
  }
.bm-item-list{
    padding: 0.8em;
}
.menu-item{
    position: relative;
    font-size: 2em;
    text-decoration: none;
    color: #fff;
    font-family: 'ProximaNova';
    padding: 10px;
}
.menu-item:before{
    content: '';
    position: absolute;
    width: 100%;
    height: 0;
    bottom: 0;
    left: 0;
    background-color: #fff;
    visibility: hidden;
    transition: all 0.5s  ease in out;
}
.active-item{
    font-weight: 600;

}
.active-item:before,.menu-item:hover:before{
    visibility: visible;
    height: 3px;
}
